import React, {Component} from 'react';
import {Tabs, Tab} from 'material-ui/Tabs';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import 'flexboxgrid/css/flexboxgrid.css';

import Home from './Home';
import Details from './Details';
import About from './About';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      slideLabel: "home"
    };
  }

  renderBody = () => {
    if (this.state.slideLabel === "home") {
      return <Home/>;
    }
    if (this.state.slideLabel === "details") {
      return <Details/>;
    }
    if (this.state.slideLabel === "about") {
      return <About/>;
    }
    return null;
  };

  handleChange = (value) => {
    console.log("Switch to " + value);
    this.setState({
      slideLabel: value,
    });
  };

  render() {
    return (
      <MuiThemeProvider>
        <div style={{backgroundColor: "#F7F8FA"}}>
          {/*Header Row*/}
          <div className="row center-xs" style={{backgroundColor: "#FFFFFF"}}>
            <div className="col-xs-12 col-sm-10 col-md-8 col-lg-6">
              <Tabs
                tabItemContainerStyle={{backgroundColor: "#FFFFFF"}}
                inkBarStyle={{backgroundColor: 'rgb(70, 160, 153)'}}
                onChange={this.handleChange}
                value={this.state.slideLabel}
                ref={e => this.navTabBar = e}
              >
                <Tab style={styles.tab} label="主页" value={"home"}/>
                <Tab style={styles.tab} label="阅享" value={"details"}/>
                <Tab style={styles.tab} label="关于我们" value={"about"}/>
              </Tabs>
            </div>
          </div>

          {this.renderBody()}

        </div>

      </MuiThemeProvider>
    )
  }
}

const styles = {
  tab: {
    color: "#727272",
    fontWeight: 400
  },
};

export default App



